import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { action } from "../modules/tools";

const ImageViewer = ({ src, alt }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    if (isOpen) {
      dispatch(action.setCanScroll(false));
    } else {
      dispatch(action.setCanScroll(true));
    }
  }, [isOpen]);

  return (
    <>
      <StyledImage
        src={src}
        alt={alt}
        onClick={() => setIsOpen(true)}
        onMouseEnter={() => {
          dispatch(action.setCursor("focus"));
        }}
        onMouseLeave={() => {
          dispatch(action.setCursor("normal"));
        }}
      />
      {isOpen && (
        <StyledOverlay onClick={() => setIsOpen(false)}>
          <StyledImageInOverlay src={src} alt={alt} />
        </StyledOverlay>
      )}
    </>
  );
};

export default ImageViewer;

const StyledImage = styled.img`
  width: 100%;
  object-fit: contain;
`;

const StyledOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
`;

const StyledImageInOverlay = styled.img`
  max-width: 80%;
  max-height: 80%;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
`;
