import { useEffect, useRef } from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { action } from "../../../modules/tools";
import ImageSlider from "../../../utils/ImageSlider";

const InnerCardComponent = ({
  imgConrtrollHoverEffect,
  title,
  text1,
  text2,
  gitAddress,
  imgUrlArray,
  stackArray,
  projectLink,
}) => {
  const imgControlBox = useRef();
  useEffect(() => {
    if (imgControlBox.current) {
      [...imgControlBox.current.children].forEach((item) => {
        item.onmouseenter = () => {
          imgConrtrollHoverEffect(true);
        };
        item.onmouseleave = () => {
          imgConrtrollHoverEffect(false);
        };
      });
    }
  }, [imgControlBox]);

  const dispatch = useDispatch();

  return (
    <InnerCardComponentBox className="">
      <CardBannerBox>
        <TitleBox className="">{title}</TitleBox>
        <StackBox className="">
          {stackArray.map((item, index) => {
            switch (item) {
              case "HTML":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/html.png"
                    alt=""
                  />
                );
              case "CSS":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/css.png"
                    alt=""
                  />
                );
              case "Javascript":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/javascript.png"
                    alt=""
                  />
                );
              case "AWS":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/aws.png"
                    alt=""
                  />
                );
              case "Redux":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/redux.png"
                    alt=""
                  />
                );
              case "MySQL":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/mysql.png"
                    alt=""
                  />
                );
              case "React":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/react.png"
                    alt=""
                  />
                );
              case "Typescript":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/typescript.png"
                    alt=""
                  />
                );
              case "GitHub":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/github.png"
                    alt=""
                  />
                );
              case "NodeJS":
                return (
                  <img
                    key={`${item}-${index}`}
                    src="/img/icon/nodejs.png"
                    alt=""
                  />
                );
            }
          })}
        </StackBox>
      </CardBannerBox>

      <ContentBox className="">
        <ImageSlider images={imgUrlArray} />
        <DetailBox>
          <IntroBox>{text1}</IntroBox>
          <EtcBox>{text2}</EtcBox>
          <GitAddressBox className="">
            <LinkBox display={gitAddress ? "flex" : "none"}>
              <img src="/img/icon/github.png" alt="" />
              <a
                href={gitAddress}
                target={"_blank"}
                onMouseEnter={(e) => {
                  dispatch(action.setCursor("focus"));
                }}
                onMouseLeave={(e) => {
                  dispatch(action.setCursor("normal"));
                }}
              >
                {"깃허브 링크"}
              </a>
            </LinkBox>
            <LinkBox display={projectLink ? "flex" : "none"}>
              <img src="/img/icon/link.svg" alt="" />
              <Link
                to={projectLink}
                onMouseEnter={(e) => {
                  dispatch(action.setCursor("focus"));
                }}
                onMouseLeave={(e) => {
                  dispatch(action.setCursor("normal"));
                }}
              >
                프로젝트 링크
              </Link>
            </LinkBox>
          </GitAddressBox>
        </DetailBox>
      </ContentBox>
    </InnerCardComponentBox>
  );
};

export default InnerCardComponent;

const InnerCardComponentBox = styled.div`
  width: 100%;
  border-radius: 15px;
  background-color: #cde990;
  padding-bottom: 30px;
  word-break: break-all;
`;

const TitleBox = styled.div`
  color: #0f0f0f;
  margin-left: 5%;
  margin-top: 30px;
  border-radius: 15px;
  width: 40%;
  padding: 20px 0px;
  background-color: #aacb73;
  font-size: 1.5rem;
  font-weight: 600;
`;

const ContentBox = styled.div`
  margin: auto;
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
  width: 90%;
  background-color: #aacb73;
  border-radius: 15px;
  height: 500px;
  padding: 15px;
  @media only screen and (max-width: 1200px) {
    flex-direction: column;
  }
`;

const DetailBox = styled.div`
  margin: 30px 0;
  width: 100%;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 30px;
  text-align: left;
`;

const IntroBox = styled.div``;

const EtcBox = styled.div``;

const StackBox = styled.div`
  margin-top: 30px;
  background-color: #aacb73;
  width: 45%;
  height: 100%;
  font-size: 1.5rem;
  padding: 20px 0px;
  border-radius: 15px;
  display: flex;
  justify-content: space-evenly;
  img {
    height: 2rem;
  }
`;

const CardBannerBox = styled.div`
  display: flex;
  align-items: center;
  column-gap: 30px;
  border: 15px;
`;

const GitAddressBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  column-gap: 20px;
  row-gap: 30px;
  transition: all 0.3s linear;
  font-size: 0.8rem;
  a:link,
  a:visited,
  a:hover,
  a {
    display: flex;
    align-items: center;
    height: 2rem;
    text-decoration: none;
    font-weight: 600;
    color: #0f0f0f;
    transition: font-size 0.5s;
    cursor: none;
  }
  a:hover {
    font-size: 1rem;
  }
  img {
    height: 1.5rem;
  }
`;

const LinkBox = styled.div`
  display: ${({ display }) => display};
  align-items: center;
  column-gap: 10px;
`;
